/*
	OBJECTS
*/

.filter__form {
	position: fixed;
	width: 300px;
	margin: 40px 75px;
	float: left;
}

.filter__form form {
	margin: 15px;
}

.filter__result {
	float: left;
	width: 50%;
	margin: 40px 40px 40px 450px;
}

/*
	COMPONENTS
*/

/* No files to import found in _components//**/

/* */

/*
	TRUMPS
*/

/* No files to import found in _trumps//**/

/* */

@media all and (max-width: 992px) {

.filter__form {
	float: none;
	width: 90%;
	margin: 40px auto 40px auto;
	position: relative;
}

.filter__result {
	float: none;
	width: 90%;
	margin: 0 auto 40px auto;
}

}

